<template>
  <div class="background">
    <img class="backgroundimage" :src="bgImgSrc" />
    <template v-for="(item, index) in ElementMetrics">
      <ElementComponent
        v-bind:key="item.id"
        :Element="item"
        :DesiredElement="Desired_elementMetrics[index]"
        :Top="elements[index].element.postop"
        :Left="elements[index].element.posleft"
        :ShowDesired="ShowDesiredElements"
      ></ElementComponent>
    </template>
  </div>
</template>

<script>
import { metricHasQueryRequirements, mapMetricInputToQuery} from '@/shared/utils';
import ElementComponent from "./utils/ElementComponent.vue";

export default {
  name: "Live-picture-elements",
  props: { contextFactory: Function },
  components: {
    ElementComponent,
  },
  data() {
    return {
      bgImgSrc: null,
      ElementMetrics: [],
      Desired_elementMetrics: [],
    };
  },
  computed: {
    
    elements() {
      if (this.context.inputs && this.context.inputs.elements) {
        const elements = this.context.inputs.elements;
        return elements;
      }
    },
    ShowDesiredElements(){
      return this.context.inputs.showDesiredElements;
    },
    //Functions to get usable information form the variables
    Elements_queries() {
      const elements = this.context.inputs.elements || [];
      return elements
        .filter(e => e.element.element_value && e.element.element_value.selector)
        .map(e => mapMetricInputToQuery(e.element.element_value));
    },
    Desired_elements_queries() {
      const desired_elements = this.context.inputs.elements || [];
      return desired_elements
        .filter(e => e.element.desired_element_value && e.element.desired_element_value.selector)
        .map(e => mapMetricInputToQuery(e.element.desired_element_value));
    },
  },
  created(){
    //Create data connection with Ixon
    this.context = this.contextFactory();
    this.client = this.context.createLoggingDataClient();

    //Get image url
	  this.bgImgSrc = this.context.inputs.BackgroundImage;
    
    //Change defined variables to variables with usable information
    this.client.query(this.Elements_queries, metrics => {
      if (metrics.length) {
        this.ElementMetrics = metrics;
          
      }
	  },
	  );

    //Change defined variables to variables with usable information
    this.client.query(this.Desired_elements_queries, metrics => {
      if (metrics.length) {
        this.Desired_elementMetrics = metrics;
          
      }
	  },
	  );
  }
};
</script>

<style lang="scss" scoped>
@import '@/shared/styles/card';

.background {
  width: 100%;
  position: relative;

  .backgroundimage {

    width: 100%;
    object-fit: contain;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
  }
}
</style>