<template>
  <div class="element" id="element" :style="cssElement">
    <div class="elementvalue" id="berwaarde">
      {{ this.DesiredElement[0].value }}
    </div>
    <div class="elementvalue" id="meetwaarde">
      {{ this.Element[0].value }}
    </div>
  </div>
</template>

<script>
export default {
  name: "ElementComponent",
  props: ["Element","DesiredElement", "Top", "Left","ShowDesired"],
  computed: {
    cssElement() {
      return {
        "--PosTop": this.Top + "%",
        "--PosLeft": this.Left + "%",
      };
    },
  },
};
</script>

<style>
/*Define size of element*/
.element {
  position: absolute;
}

/*Define placement of element*/
#element {
  top: var(--PosTop);
  left: var(--PosLeft);
}

/*Style value of element*/
.elementvalue {
  width: 100%;
  height: inherit;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.9vw;
  margin: 5px;
  border-radius: 5%;
  background-color: rgba(0, 124, 186, 0.8);
  font-family: 'Roboto';
}
#berwaarde{
  color: #fff;
}
#meetwaarde{
  color: #00ff00;
}
</style>